<template>
  <div>
    <form  @submit.prevent="submit">
      <div class="row justify-content-center">
        <div class="col-6 p-2" style="font-size:12px;">
          <div class="shadow p-3 rounded-lg">
          <!-- INPUT -->
          <div class="sm-form">
            <input
              type="text"
              id="id"
              name="id"
              class="form-control form-control-sm"
              placeholder="id"
              v-model="vdata.id"
              disabled
            />
          </div>
          <div class="table-responsive">
              <table class="table table-sm table-striped table-bordered">
                   <tr>        
                      <td class="capitalize">wilayah</td>
                      <td class="pl-2 pr-2">:</td>
                      <td>
                         <v-select :options="wilayah" label="kecamatan" v-model="vdata.wilayah" :reduce="e => e.id"></v-select>
                      </td>
                  </tr>
                   <tr>        
                      <td class="capitalize">tanggal</td>
                      <td class="pl-2 pr-2">:</td>
                      <td>
                          <div class="sm-form ">
                              <input type="date" id="tanggal" name="tanggal" class="form-control form-control-sm" placeholder="tanggal" v-model="vdata.tanggal" >
                          </div>
                      </td>
                  </tr>
                   <tr>        
                      <td class="capitalize">odp</td>
                      <td class="pl-2 pr-2">:</td>
                      <td>
                          <div class="sm-form ">
                              <input type="number" id="odp" name="odp" class="form-control form-control-sm" placeholder="odp" v-model="vdata.odp" >
                          </div>
                      </td>
                  </tr>
                   <tr>        
                      <td class="capitalize">pdp</td>
                      <td class="pl-2 pr-2">:</td>
                      <td>
                          <div class="sm-form ">
                              <input type="number" id="pdp" name="pdp" class="form-control form-control-sm" placeholder="pdp" v-model="vdata.pdp" >
                          </div>
                      </td>
                  </tr>
                   <tr>        
                      <td class="capitalize">positif</td>
                      <td class="pl-2 pr-2">:</td>
                      <td>
                          <div class="sm-form ">
                              <input type="number" id="positif" name="positif" class="form-control form-control-sm" placeholder="positif" v-model="vdata.positif" >
                          </div>
                      </td>
                  </tr>
                   <tr>        
                      <td class="capitalize">sembuh</td>
                      <td class="pl-2 pr-2">:</td>
                      <td>
                          <div class="sm-form ">
                              <input type="number" id="sembuh" name="sembuh" class="form-control form-control-sm" placeholder="sembuh" v-model="vdata.sembuh" >
                          </div>
                      </td>
                  </tr>
                    <tr>        
                      <td class="capitalize">meninggal</td>
                      <td class="pl-2 pr-2">:</td>
                      <td>
                          <div class="sm-form ">
                              <input type="number" id="meninggal" name="meninggal" class="form-control form-control-sm" placeholder="meninggal" v-model="vdata.meninggal" >
                          </div>
                      </td>
                  </tr>
              </table>
          </div>
          <!-- END INPUT -->
          <!-- BUTTON PART -->
          <div class="mt-3 p-3">
            <button
              type="submit"
              class="btn btn-sm"
              :class="
                btn == 'tambah'
                  ? 'btn-primary'
                  : btn == 'update'
                  ? 'btn-success'
                  : 'btn-danger'
              "
            >
              <span class="typcn typcn-info"></span> Proses  {{ btn }}
            </button>
            <div class="float-right">
              <button
                type="button"
                v-show="btn!='tambah'"
                @click="
                  btn = 'tambah';
                  vdata = {};
                "
                class="btn btn-sm btn-dark"
              >
                <span class="typcn typcn-edit"></span> Tambah
              </button>
              <button
                type="button"
                @click="btn = 'delete'"
                v-show="btn!='tambah'"
                class="ml-3 btn btn-sm btn-danger"
              >
                <span class="typcn typcn-delete"></span> Delete
              </button>
            </div>
          </div>
          </div>
          <!-- END BUTTON PART -->
        </div>
        <div class="col-6 p-2" style="font-size:12px;">
          <div class="shadow rounded-lg p-2">
            <!-- TABLE PART -->
            <div v-if="datanya.length>0" class="p-2">
             <div class="text-right">
                <vue-json-excel :data="datanya">
                  <button type="button" class="btn btn-sm btn-dark">
                    Export <span class="typcn typcn-chart-area"></span>
                  </button>
                </vue-json-excel>
              </div>
                <Btables :tables="datanya"  :cari="['']" :hide="['wilayah']" @selected="ambil" ref="btable" />
            </div>
            <!-- END TABLE PART -->
         </div>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
// TOTURIAL 
// ganti source autophp, ganti source component btable, ganti path table sdb
import autophp from "@/plugins/autophp_gis.js"; // ganti source nya
import Btables from "@/components/Btables.vue"; //component btable nya
let sdb = new autophp();
export default {
    layout:'app_gis',
  components: {
    Btables,
  },
  data() {
    return {
      selected: false,
      vdata: {},
      btn: "tambah",
      datanya:[],
      wilayah:[]
    };
  },
  methods: {
    app_gis_covid_balangan(){
      this.$refs.btable.getData()
    },
    getData(){
      sdb.collection("app_gis_covid_balangan").doc().select(`select tb1.id,tb1.wilayah,tb2.kecamatan,tb1.tanggal,tb1.odp,tb1.pdp,tb1.positif,tb1.meninggal,tb1.sembuh from app_gis_covid_balangan tb1 left join app_gis_wilayah_balangan tb2 on tb2.id=tb1.wilayah`).then(res => {
        this.datanya=res;
        this.$forceUpdate();
      });
      sdb.collection("app_gis_wilayah_balangan").doc().select(`select * from app_gis_wilayah_balangan`).then(res => {
          this.wilayah=res;
          this.$forceUpdate();
      });
      
    },
    ambil(data) {
      this.vdata = data;
      this.btn = "update";
      this.$forceUpdate();
    },
    async submit() {
      let that=this
      if (confirm("Apakah yakin proses dilanjutkan ?")) {
        if (this.btn == "tambah") {
          //idnya letak di vdata
          delete this.vdata.id;
          await sdb
            .collection("app_gis_covid_balangan")
            .doc()
            .set(this.vdata)
              that.getData();
            // that.nuxt.emit('getData')
        } else if (this.btn == "update") {
         await sdb
            .collection("app_gis_covid_balangan")
            .doc()
            .set(this.vdata)
              that.getData();
            // that.nuxt.emit('getData')
        } else if (this.btn == "delete") {
          await sdb
            .collection("app_gis_covid_balangan")
            .doc(this.vdata.id)
            .delete()
              that.getData();
            // that.nuxt.emit('getData')
        }
        // this.nuxt.emit('getData','ok')
      }
    },
  },
  async mounted() {
    this.getData();
  },
};
</script>